<template><markdown-wrapper><h1 id="notification">Notification <a class="header-anchor" href="#notification" aria-hidden="true">&#128279;</a></h1>
<blockquote>
<p>Displays a global notification message at a corner of the page.</p>
</blockquote>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<p>Click on the button below to show a notification. By default, it is dismissible with a close button, and will dismiss automatically after 5000ms (both are configurable).</p>
<notification-example ref="notification-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Simplest Notification<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify2"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>No Auto-dismiss Notification<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-comment">// example with callback</span>
      <span class="hljs-comment">// pass a String as the notification content</span>
      notify () {
        <span class="hljs-keyword">this</span>.$notify(<span class="hljs-string">'This is a simple notify msg.'</span>, () =&gt; {
          <span class="hljs-comment">// callback after dismissed</span>
          <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'dismissed'</span>)
        })
      },
      <span class="hljs-comment">// example with Promise and options</span>
      notify2 () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Title'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'This notification will not dismiss automatically.'</span>,
          <span class="hljs-attr">duration</span>: <span class="hljs-number">0</span>
        }).then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-comment">// resolve after dismissed</span>
          <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'dismissed'</span>)
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- notification-example.vue --&gt;</span>
</code></pre>
<h2 id="types">Types <a class="header-anchor" href="#types" aria-hidden="true">&#128279;</a></h2>
<p>There're 4 optional types of notification: <code v-pre>info</code> / <code v-pre>success</code> / <code v-pre>warning</code> / <code v-pre>danger</code>.</p>
<p>Notification with specific type will has a default icon on the left, you can also change or remove the icon by <code v-pre>icon</code> option.</p>
<notification-types ref="notification-types"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>Info<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>Success<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span>&gt;</span>Warning<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span>&gt;</span>Danger<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      info () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'info'</span>,
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Heads up!'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'This alert needs your attention, but it\'s not super important.'</span>
        })
      },
      success () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'success'</span>,
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Well done!'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'You successfully read this important alert message.'</span>
        })
      },
      warning () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'warning'</span>,
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Warning!'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'Better check yourself, you\'re not looking too good.'</span>
        })
      },
      danger () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'danger'</span>,
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Oh snap!'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'Change a few things up and try submitting again.'</span>
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- notification-types.vue --&gt;</span>
</code></pre>
<h2 id="placements">Placements <a class="header-anchor" href="#placements" aria-hidden="true">&#128279;</a></h2>
<p>Notifications can be placed on any corner on a page.</p>
<p>The <code v-pre>position</code> prop defines which corner a notification will slide in. It can be <code v-pre>top-right</code> (default), <code v-pre>top-left</code>, <code v-pre>bottom-right</code> or <code v-pre>bottom-left</code>.</p>
<notification-placements ref="notification-placements"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify('top-right')"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Top Right (Default)<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify('bottom-right')"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Bottom Right<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify('bottom-left')"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Bottom Left<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify('top-left')"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Top Left<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      notify (placement) {
        <span class="hljs-keyword">this</span>.$notify({
          placement, <span class="hljs-comment">// equal to `placement: placement` in ES6</span>
          title: <span class="hljs-string">'Title'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">`This is a notify msg at <span class="hljs-subst">${placement}</span>.`</span>
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- notification-placements.vue --&gt;</span>
</code></pre>
<h2 id="dismissible">Dismissible <a class="header-anchor" href="#dismissible" aria-hidden="true">&#128279;</a></h2>
<p>By default a notification is dismissible with a close button, you can hide it by setting <code v-pre>dismissible</code> to <code v-pre>false</code>.</p>
<notification-without-dismiss-btn ref="notification-without-dismiss-btn"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"notify"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Notification Without Dismiss Button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      notify () {
        <span class="hljs-keyword">this</span>.$notify({
          <span class="hljs-attr">title</span>: <span class="hljs-string">'Title'</span>,
          <span class="hljs-attr">content</span>: <span class="hljs-string">'This is a notification without dismiss btn.'</span>,
          <span class="hljs-attr">dismissible</span>: <span class="hljs-literal">false</span>
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- notification-without-dismiss-btn.vue --&gt;</span>
</code></pre>
<h2 id="global-method">Global method <a class="header-anchor" href="#global-method" aria-hidden="true">&#128279;</a></h2>
<p><code v-pre>$notify(options, callback)</code> global method for <code v-pre>Vue.prototype</code> will be added <strong>if uiv is installed</strong>.</p>
<p>Note that the dismissed callback is optional.</p>
<p>The method will return a <code v-pre>Promise</code> object that resolve while the notification is dismissed (if supported by browser or with es6 promise polyfill).</p>
<h2 id="import-individually">Import individually <a class="header-anchor" href="#import-individually" aria-hidden="true">&#128279;</a></h2>
<p>If you prefer importing <code v-pre>Notification</code> individually:</p>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> { Notification } <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv'</span>
</code></pre>
<p>The corresponding method is <code v-pre>Notification.notify</code>, with same parameters as above.</p>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="notification-2"><a href="https://github.com/wxsms/uiv/blob/master/src/services/notification/Notification.vue">Notification</a> <a class="header-anchor" href="#notification-2" aria-hidden="true">&#128279;</a></h2>
<p>These props are used as <code v-pre>options</code> in the methods above.</p>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>title</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The notification title.</td>
</tr>
<tr>
<td><code v-pre>content</code></td>
<td>String</td>
<td></td>
<td></td>
<td>The notification content.</td>
</tr>
<tr>
<td><code v-pre>html</code></td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td></td>
<td>Allow HTML in content.</td>
</tr>
<tr>
<td><code v-pre>type</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Support: <code v-pre>info</code> / <code v-pre>success</code> / <code v-pre>warning</code> / <code v-pre>danger</code>.</td>
</tr>
<tr>
<td><code v-pre>duration</code></td>
<td>Number</td>
<td>5000</td>
<td></td>
<td>Dismiss after milliseconds, use 0 to prevent self-closing.</td>
</tr>
<tr>
<td><code v-pre>dismissible</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show dismiss button.</td>
</tr>
<tr>
<td><code v-pre>placement</code></td>
<td>String</td>
<td>top-right</td>
<td></td>
<td>Support: <code v-pre>top-right</code> / <code v-pre>top-left</code> / <code v-pre>bottom-right</code> / <code v-pre>bottom-left</code>.</td>
</tr>
<tr>
<td><code v-pre>icon</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Custom icon class, use an empty string to disable icon.</td>
</tr>
<tr>
<td><code v-pre>custom-class</code></td>
<td></td>
<td></td>
<td></td>
<td>Custom classes to alert, anything that can work with <code v-pre>v-bind:class</code> or <code v-pre>:class</code>.</td>
</tr>
<tr>
<td><code v-pre>offset</code></td>
<td>Number</td>
<td>15</td>
<td></td>
<td>The space in px between notifications.</td>
</tr>
<tr>
<td><code v-pre>offset-x</code></td>
<td>Number</td>
<td>15</td>
<td></td>
<td>The horizontal offset in px while displaying notification.</td>
</tr>
<tr>
<td><code v-pre>offset-y</code></td>
<td>Number</td>
<td>15</td>
<td></td>
<td>The vertical offset in px while displaying notification.</td>
</tr>
</tbody>
</table></div></markdown-wrapper></template><script>
  

  

  

  
export default {components:{'notification-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn @click=\"notify\" type=\"primary\">Simplest Notification</btn>\n    <btn @click=\"notify2\" type=\"primary\">No Auto-dismiss Notification</btn>\n  </section>\n</div>",
    methods: {
      // example with callback
      // pass a String as the notification content
      notify () {
        this.$notify('This is a simple notify msg.', () => {
          // callback after dismissed
          console.log('dismissed')
        })
      },
      // example with Promise and options
      notify2 () {
        this.$notify({
          title: 'Title',
          content: 'This notification will not dismiss automatically.',
          duration: 0
        }).then(() => {
          // resolve after dismissed
          console.log('dismissed')
        })
      }
    }
  },'notification-types':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn @click=\"info\" type=\"info\">Info</btn>\n    <btn @click=\"success\" type=\"success\">Success</btn>\n    <btn @click=\"warning\" type=\"warning\">Warning</btn>\n    <btn @click=\"danger\" type=\"danger\">Danger</btn>\n  </section>\n</div>",
    methods: {
      info () {
        this.$notify({
          type: 'info',
          title: 'Heads up!',
          content: 'This alert needs your attention, but it\'s not super important.'
        })
      },
      success () {
        this.$notify({
          type: 'success',
          title: 'Well done!',
          content: 'You successfully read this important alert message.'
        })
      },
      warning () {
        this.$notify({
          type: 'warning',
          title: 'Warning!',
          content: 'Better check yourself, you\'re not looking too good.'
        })
      },
      danger () {
        this.$notify({
          type: 'danger',
          title: 'Oh snap!',
          content: 'Change a few things up and try submitting again.'
        })
      }
    }
  },'notification-placements':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <btn @click=\"notify('top-right')\" type=\"primary\">Top Right (Default)</btn>\n    <btn @click=\"notify('bottom-right')\" type=\"primary\">Bottom Right</btn>\n    <btn @click=\"notify('bottom-left')\" type=\"primary\">Bottom Left</btn>\n    <btn @click=\"notify('top-left')\" type=\"primary\">Top Left</btn>\n  </section>\n</div>",
    methods: {
      notify (placement) {
        this.$notify({
          placement, // equal to `placement: placement` in ES6
          title: 'Title',
          content: `This is a notify msg at ${placement}.`
        })
      }
    }
  },'notification-without-dismiss-btn':{"template":"<div class=\"markdown-live-example\">\n  <btn @click=\"notify\" type=\"primary\">Notification Without Dismiss Button</btn>\n</div>",
    methods: {
      notify () {
        this.$notify({
          title: 'Title',
          content: 'This is a notification without dismiss btn.',
          dismissible: false
        })
      }
    }
  }}}</script>


